.sidenav {
    width:6%;
    display: flex;
    flex-direction: column;
    background-color: var(--main-nav-color);
    justify-content: flex-start;

    -webkit-transform-origin: left;

            transform-origin: left;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    transition-duration: 0.2s;
}

.sidenav:hover {
    -webkit-transform-origin: left;
            transform-origin: left;
    text-align: center;
    -webkit-transform: scaleX(1.5);
            transform: scaleX(1.5);
    transition-duration: 0.2s;
}

/* sidenav icons */

.sidenav a {
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    transition-duration: 0.2s;
}

.sidenav:hover a {
    -webkit-transform-origin: center;
            transform-origin: center;
    text-align: center;
    -webkit-transform: scaleX(0.66);
            transform: scaleX(0.66);
    transition-duration: 0.2s;
}

/* menu icons*/

.icon {
    font-size: 1.5rem;
    margin-left:auto;
    margin-right:auto;
    transition: text-shadow 0.3s, opacity 0.3s, color 0.3s;
}

.icon:hover {
    text-shadow: 0px 0px 30px;
    opacity: 1.5;
    transition: text-shadow 0.3s, opacity 0.3s, color 0.3s;
}

#home:hover, #home.active{
    color:greenyellow;
}

#map:hover, #map.active{
    color:#7DE2D1;
}

#music:hover, #music.active{
    color:greenyellow;
}

#art:hover, #art.active{
    color:rgb(213, 121, 255);
}

#weather:hover, #weather.active{
    color:rgb(64, 147, 255);
}

#hobby:hover, #hobby.active{
    color:#FFABBC;
}

.sidenav-center-align {
    display: flex;
    text-align: center;
    justify-content: space-around;
}

.menu {
    flex: 1 1;
    padding-top: 5vh;
}

.category {
    flex: 3 1;
    padding-top: 10vh;
    padding-bottom: 10vh;
    flex-direction: column;
}
* {
    margin: 0;
    font-family: 'Open Sans Condensed', sans-serif;
    color: var(--main-text-color);
}

body {
    background-color:var(--main-bg-color);
}

.info-block {
    opacity: 0.7;
}

.content {
    height: 100vh;
    display: flex;
    overflow: hidden;
}

.main-content {
    width: 94%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.navbar {
    height: 23%;
    display: flex;
}

.page {
    width:100%;
    margin:6vh 0 0 5vw;
}

.head h1 {
    justify-self: left;
    letter-spacing: 2px;
    margin-top: 4vh;
    margin-left:1rem;
}
.container {
    width:96%;
    display:flex;
    justify-content:space-around;
    height: 75%;
    margin:auto;
}

.piechart { width:17%; min-width: 160px; }
.weather { width:63%; }
.piechart , .weather{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    /* border:1px solid var(--main-border-color); */
    /* border-radius: 1rem; */
}


.title {
    height:10%;
    width: 100%;
    padding-top: 5vh;
    text-align: center;
}
.title h3 {
    letter-spacing: 2px;
    z-index: 100;
}

.charts {
    height:90%;
    display: flex;
    /* min-width: 460px; */
    flex-direction: column;
    justify-content: space-evenly;
}

.arc-chart {
    height:30%;
}
.block {
    height: 100%;
    width: 80%;
    display: block;
    margin-left:auto;
    margin-right:auto;
    overflow: hidden;
}

.fade-enter {
    opacity: 0.01;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in;
}

.fade-exit {
    opacity: 1;
}

.fade-exit.fade-exit-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
}

section.route-section {
    position: absolute;
    height: 100%;
    width: 94%;
    top: 0;
    left: 6%;
}
